* {
  margin: 0 auto;
}

body {
   font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif; 
   font-weight: 300;
/*  background: #F4F4F5;
*/  scroll-behavior: smooth;
}

section {
  text-align: center;
  -webkit-animation: fadeIn .75s ease-in;
          animation: fadeIn .75s ease-in;
}

p {
  width: 70%;
  padding: 1em;
  font-size: 1.5em;
}

h4 {
  text-align: center;
  font-size: 2em;
  padding: 1em;
  color: #AC6C7E;
}

.page {
  height: 100vh;
  -webkit-animation: fadeIn 2s ease-in;
          animation: fadeIn 2s ease-in;
  margin-top: 2em;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding: 1em;
  margin-top: 1em;
}

.grow { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }
.grow:hover { -webkit-transform: scale(1.1); -ms-transform: scale(1.1); transform: scale(1.1); }



@-webkit-keyframes fadeIn {
  from {opacity: 0} 
  to {opacity: 1}
}

@keyframes fadeIn {
  from {opacity: 0} 
  to {opacity: 1}
}

