/*PROJECTS*/
#projects {
    background: url('../../images/projects.jpg');
    padding: 3em;
}

.projectImages {
/*  width: 25em;
*/  height: 21em;
  border-radius: 25px;
  padding: 1em;
}

.proj {
  display: -ms-flexbox;
  display: flex;
  height: 25em;
  padding: 3em;
}

/*.projMobile {
  display: flex;
  height: 20em;  
}*/

.projDesc {
  width: 25em;
    font-size: .8em;

}

.blue:target {
  background: red;
}
#projButtons {
	-ms-flex-align: center;
	    align-items: center;
/*	background: lightblue;
*/  width: 100%;

}

.btn {
  border-radius: 5px;
  padding: 15px 25px;
  font-size: 1.3em;
  text-decoration: none;
  margin: 20px;
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.btn:active {
  -ms-transform: translate(0px, 5px);
      transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
  -webkit-box-shadow: 0px 1px 0px 0px;
          box-shadow: 0px 1px 0px 0px;
}

.blue {
    background: #F5F7FA;
    -webkit-box-shadow: 0 .4vw 0 hsl(214, 16%, 86%);
            box-shadow: 0 .4vw 0 hsl(214, 16%, 86%);
    color: black;
}

.blue:hover {
    background: #E6E9ED;
}


.select {
    background: #CBE4DF;
/*    box-shadow: 0 .4vw 0 hsl(164, 5%, 95%);
*/    color: black;
}

.select:hover {
    background: #E8F3F0;
}


.mobile {
  height: 30em;
}


@media only screen and (max-width: 500px) {

  .proj {
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0;
    padding-bottom: 2em;
    font-size: .8em;
    margin-bottom: 7em;
  }

  .projectImages {
/*  width: 25em;
*/  height: 10em;

  }
  .btn {
        padding: .5em 1em;
  }

  .projects {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .projDesc {
    padding-top: 2em;
  }

  .projDesc p {
    padding: 0;
  }

  .mobile {
    height: 20em;
    padding-top: 1em;
  }
}



